export default {
    data() {
        return {};
    },
    onLoad() {
        console.log(111)
    },
    methods: {
        autuWXLogin(e) {
            var _self = this;
            console.log('用户信息', e.detail);
            uni.login({
                success: res => {
                    console.log(res.code); // code
                    uni.request({
                        url:'http://192.168.1.218/api/v1.1.0/users/authorization/',
                        method: 'POST',
                        data: {
                            code: res.code
                        },
                        headers: {
                            'Content-Type': 'application/json'
                        },
                        success: res => {
                            console.log(res);
                            // uni.setStorageSync('phone', this.phone);
                            uni.setStorageSync('token', res.data.token);
                            // _self.global_.phone = this.phone;
                            _self.global_.token = res.data.token;
                            if (res.statusCode == 200) {
                                console.log(res);
                                uni.reLaunch({
                                    url: '../login/login'
                                });
                            }
                        },
                        fail: () => {},
                        complete: () => {}
                    });
                }
            });
        }
    }
};
